<!--related products--><section class="related-products"><div class="container"><div class="main-title"><h2>{{ $t("new_arrivals") }}</h2><div class="decore-title"><img src="@/assets/images/favicon.png" alt="decore image"></div></div><div class="content"><div class="row"><div class="col-lg-3 col-md-6" v-for="product in relatedProducts" :key="product.id"><div class="related-box"><div class="image"><router-link :to="`/products/${product.id}`"> <img class="img-fluid" :src="product.image" :alt="product.image_alt"></router-link><div class="fav"><a class="btn" href="javascript:void(0);"> <i class="bi bi-heart"></i></a></div><div class="layer"><a class="btn" href="javascript:void(0);" @click="addToCart()">{{ $t('add_to_cart') }}</a></div></div><div class="details"><div class="title"><h3><router-link :to="`/products/${product.id}`">{{ product.title }}</router-link></h3></div></div></div></div></div></div></div></section><!--related products-->