import {
    createStore
} from 'vuex'
import axios from 'axios'
axios.defaults.baseURL = 'https://demoyoursite.net/taha/back/in-arabia/api/';
// axios.defaults.baseURL = 'http://127.0.0.1:8000/api/';

let locale = window.location.pathname.replace(/^\/([^/]+).*/i, "$1")

//update localstorage
function updateLocalStorage(cart) {
    localStorage.setItem("cart", JSON.stringify(cart));
}

export default createStore({
    state: {
        loading: true,
        formSent: null,
        contactErrorsApi: null,
        homeData: [],
        aboutData: [],
        productsData: [],
        settingsData: [],
        cart: []
    },
    getters: {
        productQuantity: state => product => {
            const item = state.cart.find(i => i.id === product.id)
            if (item) {
                return item.quantity
            } else {
                return null
            }
        },
        cartItems: state => {
            return state.cart
        },
        cartTotal: state => {
            return state.cart.reduce((a, b) => a + (b.price * b.quantity), 0)
        },
        totalProducts: state => {
            return state.cart.length
        }
    },
    mutations: {
        setHomeData: (state, homeData) => (state.homeData = homeData),
        setAboutData: (state, aboutData) => (state.aboutData = aboutData),
        setProductsData: (state, productsData) => (state.productsData = productsData),
        setSettingsData: (state, settingsData) => (state.settingsData = settingsData),

        addToCart(state, product) {
            let item = state.cart.find(i => i.id === product.id)

            if (item) {
                item.quantity++
            } else {
                state.cart.push({
                    ...product,
                    quantity: 1
                })
            }
            updateLocalStorage(state.cart)
        },

        removeFromCart(state, product) {
            let item = state.cart.find(i => i.id === product.id)

            if (item) {
                if (item.quantity > 1) {
                    item.quantity--
                } else {
                    state.cart = state.cart.filter(i => i.id !== product.id)
                }
            }
            updateLocalStorage(state.cart)
        },

        destroyCart(state) {
            let cart = state.cart;

            if (cart) {
                cart = null
            }
            updateLocalStorage(cart)
        },

        updateCartFromStorage(state) {
            const cart = localStorage.getItem("cart");

            if (cart) {
                state.cart = JSON.parse(cart)
            }
        }
    },
    actions: {
        addToCart: (context, product) => {
            context.commit('addToCart', product);
        },

        async getHomeData({
            commit
        }) {
            this.state.loading = true;
            await axios
                .get("home", {
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    params: {
                        lang: locale
                    }
                })
                .then((result) => {
                    this.state.loading = false;
                    commit("setHomeData", result.data.data);
                })
                .catch((error) => {
                    console.log(error);
                });
        },
        async getAboutData({
            commit
        }) {
            this.state.loading = true;
            await axios
                .get("about", {
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    params: {
                        lang: locale
                    }
                })
                .then((result) => {
                    commit("setAboutData", result.data.data);
                    this.state.loading = false;
                })
                .catch((error) => {
                    console.log(error);
                });
        },
        async getProductsData({
            commit
        }) {
            this.state.loading = true;
            await axios
                .get("products", {
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    params: {
                        lang: locale
                    }
                })
                .then((result) => {
                    commit("setProductsData", result.data.data);
                    this.state.loading = false;
                })
                .catch((error) => {
                    console.log(error);
                });
        },
        async getSettingsData({
            commit
        }) {
            await axios
                .get("settings", {
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    params: {
                        lang: locale
                    }
                })
                .then((result) => {
                    commit("setSettingsData", result.data.data);
                })
                .catch((error) => {
                    console.log(error);
                });
        },
        async storeFormData({}, contactFormData) {
            await axios
                .post("contact", {
                    contactFormData
                })
                .then(() => {
                    this.state.contactErrorsApi = null
                })
                .catch((error) => {
                    this.state.contactErrorsApi = error.response;
                });
        },
    },
})