<!--breadcrumb--><nav class="main-breadcrumb" aria-label="breadcrumb" v-if="settingsData" :style="{backgroundImage: `url(${settingsData.breadcrumb_bg})`}"><div class="container"> <div class="breadcrumb-title"><h2>{{ $t("contact_us") }}</h2></div><nav aria-label="breadcrumb"><ol class="breadcrumb"><li class="breadcrumb-item"><router-link to="/" exact>{{ $t("home") }}</router-link></li><li class="breadcrumb-item active" aria-current="page">{{ $t("contact_us") }}</li></ol></nav></div></nav><!--breadcrumb--><!--toast part--><div class="position-fixed bottom-0 p-3 toast-part"><div class="toast align-items-center text-white bg-success border-0" :class="{ 'show': sent }" role="alert" aria-live="assertive" aria-atomic="true"><div class="d-flex"><div class="toast-body">{{ $t("mesage_sent_successfully") }}</div><button class="btn-close btn-close-white" type="button" data-bs-dismiss="toast" aria-label="Close"></button></div></div></div><div class="position-fixed bottom-0 p-3 toast-part"><div class="toast align-items-center text-white bg-danger border-0" :class="{ 'show': recapchaError }" role="alert" aria-live="assertive" aria-atomic="false"><div class="d-flex"><div class="toast-body">{{ $t("recapcha_error") }}</div><button class="btn-close btn-close-white" type="button" data-bs-dismiss="toast" aria-label="Close"></button></div></div></div><!--toast part--><!--contact--><section class="contact"><div class="container"><div class="info-area"><div class="row"><div class="col-md-4 col-lg-4"><div class="info-box"><i class="bi bi-envelope"> </i><h3>{{ $t("email") }}</h3><a :href="'mailto:'+settingsData.email">{{ settingsData.email }}</a></div></div><div class="col-md-4 col-lg-4">  <div class="info-box"><i class="bi bi-telephone"></i><h3>{{ $t("phone") }}</h3><a :href="'tel:'+settingsData.phone" dir="ltr">{{ settingsData.phone }}</a></div></div><div class="col-md-4 col-lg-4"><div class="info-box"><i class="bi bi-pin-map"></i><h3>{{ $t("address") }}</h3><span>{{ settingsData.address }}</span></div></div></div></div><div class="form-area"><div class="contact-box"><div class="form-content"><form class="g-3" enctype="multipart/form-data" @submit.prevent="handleContactForm"><div class="row"><div class="col-lg-12"><div class="form-group input-group"><input class="form-control" type="text" :placeholder="$t('name')" name="name" v-model="contactFormData.name"><div class="invalid-feedback" v-if="contactErrors.name &amp;&amp; !sent" v-for="error in contactErrors.name">{{ error }}</div></div></div><div class="col-lg-12"><div class="form-group input-group"><input class="form-control" type="text" :placeholder="$t('phone')" name="phone" v-model="contactFormData.phone"><div class="invalid-feedback" v-if="contactErrors.phone &amp;&amp; !sent" v-for="error in contactErrors.phone">{{ error }}</div></div></div><div class="col-lg-12"><div class="form-group input-group"><input class="form-control" type="text" :placeholder="$t('email')" name="email" v-model="contactFormData.email"><div class="invalid-feedback" v-if="contactErrors.email &amp;&amp; !sent" v-for="error in contactErrors.email">{{ error }}</div></div></div><div class="col-lg-12"><div class="form-group input-group"><textarea class="form-control" rows="6" :placeholder="$t('message')" name="message" v-model="contactFormData.message"></textarea><div class="invalid-feedback" v-if="contactErrors.message &amp;&amp; !sent" v-for="error in contactErrors.message">{{ error }}</div></div></div><div class="col-lg-12 mb-3"><vue-recaptcha @verify="onVerify" @expired="onCaptchaExpired" ref="recaptcha" sitekey="6LeMuKkeAAAAAJGnqbyd2jEWdTS65wP5Xwtw9H81"></vue-recaptcha></div><div class="col-lg-12 text-center"><div class="form-group input-group"><button class="btn btn-danger hvr-sweep-to-top" type="submit" :disabled="sending == true"> <span v-if="!sending">{{ $t('send') }}</span><span v-if="sending">{{ $t('sending') }}</span><div class="spinner-border text-light" role="status" v-if="sending"><span class="visually-hidden">Loading...</span></div></button></div></div></div></form></div></div></div></div></section><!--contact --><!--map--><section class="map"><iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d55251.33564056191!2d31.22344491853406!3d30.059558098334296!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x14583fa60b21beeb%3A0x79dfb296e8423bba!2sCairo%2C%20Cairo%20Governorate!5e0!3m2!1sen!2seg!4v1634654464669!5m2!1sen!2seg" width="100%" height="450" style="border:0;" allowfullscreen="" loading="lazy"></iframe></section><!--map-->