<!--all products--><section class="all-products" v-if="productsApi"><div class="container"><div class="content"><div class="alert alert-success d-flex justify-content-between" role="alert" v-if="totalProducts"><div class="d-flex align-items-center"><svg class="bi flex-shrink-0 me-2" width="24" height="24" role="img" aria-label="Success:"><use xlink:href="#check-circle-fill"></use><symbol fill="#8fae1b" id="check-circle-fill" viewBox="0 0 16 16"><path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z"></path></symbol></svg><div>{{ totalProducts }} products added to your cart.</div></div><router-link class="btn" to="/cart">view cart</router-link></div><div class="row"><div class="col-12"><div class="results"> <div class="icons"><i class="grid-2 bi bi-grid-fill"></i><i class="grid-3 bi bi-grid-3x3-gap-fill"></i><div class="text">showing 20 of results </div></div><div class="filter"><form action=""><div class="form-group"><select class="form-select" name=""><option value="" selected disabled>categories</option><option value="">deal of the week   </option><option value="">featured products    </option><option value="">new arrivals</option></select></div></form></div></div></div><div class="box col-lg-3" v-for="product in productsApi" :key="product.id"><div class="products-box"><div class="image"><router-link :to="`/products/${product.id}`"> <img class="img-fluid" :src="product.image" :alt="product.image_alt"></router-link><div class="fav"><a class="btn" href="javascript:void(0);"><i class="bi bi-heart-fill"></i></a></div><div class="layer" v-if="productTotal(product) &lt;= 0"><a class="btn" @click="addToCart(product)">{{ $t("add_to_cart") }}</a></div></div><div class="details"><div class="title"><h3><router-link :to="`/products/${product.id}`">{{ product.title }}</router-link></h3></div></div></div></div></div></div></div></section><!--all products-->