<!--featured products--><section class="featured-products"><div class="container"><div class="main-title"><h2>{{ $t("featured_products") }}</h2><div class="decore-title"><img src="@/assets/images/favicon.png" alt=""></div></div><div class="content"><ul class="nav nav-pills" role="tablist"><li class="nav-item" role="presentation"><button class="nav-link active" id="products-all-tab" data-bs-toggle="pill" data-bs-target="#products-all" type="button" role="tab" aria-controls="products-all" aria-selected="true">{{ $t("all") }}</button></li><li class="nav-item" role="presentation" v-for="category in categoriesData" :key="category.id"><button class="nav-link" :id="'products-'+category.id+'-tab'" data-bs-toggle="pill" :data-bs-target="'#products-'+category.id" type="button" role="tab" :aria-controls="'products-'+category.id" aria-selected="false">{{ category.title }}</button></li></ul><div class="tab-content"><div class="tab-pane fade show active" id="products-all" role="tabpanel" aria-labelledby="products-all-tab"> <div class="products-content"><div class="row"><div class="col-lg-3 col-md-6 col-sm-6" v-for="product in allProductsData" :key="product.id"><div class="products-box"><div class="image"><a href="single-product.html"> <img class="img-fluid" :src="product.image" :alt="product.image_alt"></a><div class="fav" v-if="userData"><a class="btn" href="javascript:void(0);"> <i class="bi bi-heart"></i></a></div><div class="layer" v-if="userData &amp;&amp; productTotal(product) &lt;= 0"><a class="btn" @click="addToCart(product)">{{ $t("add_to_cart") }}</a></div><div class="layer" v-if="!userData"><router-link class="btn" to="/account">{{ $t("add_to_cart") }}</router-link></div></div><div class="details"><div class="title"><h3><a href="single-product.html">{{ product.title }}</a></h3></div></div></div></div></div></div></div><div class="tab-pane fade" v-for="category in categoriesData" :key="category.id" :id="'products-'+category.id" role="tabpanel" :aria-labelledby="'products-'+category.id+'-tab'"> <div class="products-content"><div class="row"><div class="col-lg-3 col-md-6 col-sm-6" v-for="product in category.products" :key="product.id"><div class="products-box"><div class="image"><a href="single-product.html"> <img class="img-fluid" :src="product.image" :alt="product.image_alt"></a><div class="fav" v-if="userData"><a class="btn" href="javascript:void(0);"> <i class="bi bi-heart-fill"></i></a></div><div class="layer" v-if="userData &amp;&amp; productTotal(product) &lt;= 0"><a class="btn" @click="addToCart(product)">{{ $t("add_to_cart") }}</a></div><div class="layer" v-if="!userData"><router-link class="btn" to="/account">{{ $t("add_to_cart") }}</router-link></div></div><div class="details"><div class="title"><h3><a href="single-product.html">{{ product.title }}          </a></h3></div></div></div></div></div></div></div></div></div></div></section><!--featured products-->