<!--toast part--><div class="position-fixed bottom-0 p-3 toast-part"><div class="toast align-items-center text-white bg-success border-0" :class="{ 'show': updated }" role="alert" aria-live="assertive" aria-atomic="true"><div class="d-flex"><div class="toast-body">{{ $t("wishlist_updated_successfully") }}</div><button class="btn-close btn-close-white" type="button" data-bs-dismiss="toast" aria-label="Close"></button></div></div></div><!--toast part--><!--new arrivals--><section class="new-arrivals" v-if="arrivalData"><div class="container"><div class="main-title"><h2>{{ $t("new_arrivals") }}</h2><div class="decore-title"><img src="@/assets/images/favicon.png" alt=""></div></div><div class="content"> <carousel class="arrivals-carousel" :items-to-show="4" :autoplay="5000" :settings="settings" :breakpoints="breakpoints" dir="ltr"><slide class="arrivals-box" v-for="product in arrivalData" :key="product.id"><div class="image"><router-link :to="`/products/${product.id}`"> <img class="img-fluid" :src="product.image" :alt="product.image_alt"></router-link><div class="fav" v-if="!userData"><router-link class="btn" to="/account"><i class="bi bi-heart"></i></router-link></div><div class="fav" v-if="userData"><form enctype="multipart/form-data" @submit.prevent="updateWishlist"><input name="product_id" :value="product.id" type="hidden"><button class="btn" type="submit"><i class="bi bi-heart-fill" v-if="userWishlist &amp;&amp; userWishlist.some(wish =&gt; wish.id == product.id)"></i><i class="bi bi-heart" v-else></i></button></form></div><div class="layer" v-if="userData &amp;&amp; productTotal(product) &lt;= 0"><a class="btn" @click="addToCart(product)">{{ $t("add_to_cart") }}</a></div><div class="layer" v-if="!userData"><router-link class="btn" to="/account">{{ $t("add_to_cart") }}</router-link></div></div><div class="details"><div class="title"><h3><router-link :to="`/products/${product.id}`">{{ product.title }}</router-link></h3></div></div></slide><template #addons><Navigation></Navigation></template></carousel></div></div></section><!--new arrivals-->