<!--breadcrumb--><nav class="main-breadcrumb" aria-label="breadcrumb" v-if="settingsData" :style="{backgroundImage: `url(${settingsData.breadcrumb_bg})`}"><div class="container"> <div class="breadcrumb-title"><h2>{{ userData.name }}</h2></div><nav aria-label="breadcrumb"><ol class="breadcrumb"><li class="breadcrumb-item"><router-link to="/" exact>{{ $t("home") }}</router-link></li><li class="breadcrumb-item active" aria-current="page">{{ $t("profile") }}</li></ol></nav></div></nav><!--breadcrumb--><!--if profile updated successfully--><div class="position-fixed bottom-0 start-0 p-3 toast-part"><div class="toast align-items-center text-white bg-success border-0" :class="{ 'show': profileUpdated }" role="alert" aria-live="assertive" aria-atomic="true"><div class="d-flex"><div class="toast-body">{{ $t("your_data_updated_successfully") }}</div><button class="btn-close btn-close-white me-2 m-auto" type="button" data-bs-dismiss="toast" aria-label="Close"></button></div></div></div><!--profile--><section class="profile"><div class="container"><div class="profile-content"><div class="row"><div class="col-lg-3"><div class="top-part"><div class="nav flex-column nav-pills shadow" id="profile_tabs" role="tablist" aria-orientation="vertical"><button class="nav-link active" id="v-pills-1-tab" data-bs-toggle="pill" data-bs-target="#v-pills-1" type="button" role="tab" aria-controls="v-pills-1" aria-selected="false">edit profile</button><button class="nav-link" id="v-pills-2-tab" data-bs-toggle="pill" data-bs-target="#v-pills-2" type="button" role="tab" aria-controls="v-pills-2" aria-selected="false">change password</button></div><div class="profile-info shadow"> <div class="image"> <img class="img-fluid" src="@/assets/images/blank.png" alt=""></div><div class="details"> <h3>{{ userData.name }}</h3><router-link class="btn btn-primary" to="/" @click="logout">{{ $t("logout") }}</router-link></div></div></div></div><div class="col-lg-9"><div class="tab-content" id="profile_tabsContent"><div class="tab-pane fade show active" id="v-pills-1" role="tabpanel" aria-labelledby="v-pills-1-tab"> <form class="g-3 shadow" enctype="multipart/form-data" @submit.prevent="handleProfileForm"><div class="row"><div class="col-lg-12"><div class="form-group"><label for="p_name">{{ $t('name') }}</label><input class="form-control" type="text" id="p_name" :placeholder="$t('name')" name="name" v-model="profileFormData.name"><div class="invalid-feedback" v-if="profileErrors.name &amp;&amp; !register_done" v-for="error in profileErrors.name">{{ error }}</div></div></div><div class="col-lg-12"><div class="form-group"><label for="p_phone">{{ $t('phone') }}</label><input class="form-control" type="text" id="p_phone" :placeholder="$t('phone')" name="phone" v-model="profileFormData.phone"><div class="invalid-feedback" v-if="profileErrors.phone &amp;&amp; !register_done" v-for="error in profileErrors.phone">{{ error }}</div></div></div><div class="col-lg-12"><div class="form-group"><label for="p_email">{{ $t('email') }}</label><input class="form-control" type="email" id="p_email" :placeholder="$t('email')" name="email" v-model="profileFormData.email"><div class="invalid-feedback" v-if="profileErrors.email &amp;&amp; !register_done" v-for="error in profileErrors.email">{{ error }}</div></div></div><div class="col-lg-12"><div class="form-group"><button class="btn btn-danger hvr-sweep-to-top" type="submit"> <span v-if="!updating">{{ $t('save_changes') }}</span><span v-if="updating">{{ $t('sending') }}</span><div class="spinner-border text-light" role="status" v-if="updating"><span class="visually-hidden">Loading...</span></div></button></div></div></div></form></div><div class="tab-pane fade" id="v-pills-2" role="tabpanel" aria-labelledby="v-pills-2-tab"> <form class="g-3 shadow" action="" novalidate><div class="row"><div class="col-lg-12"><div class="form-group"><label for="p_password">{{ $t('password') }}</label><input class="form-control" type="password" id="p_password"></div></div><div class="col-lg-12"><div class="form-group"><label for="cp_password">{{ $t('c_password') }}</label><input class="form-control" type="password" id="cp_password"></div></div><div class="col-lg-12"><div class="form-group"><button class="btn btn-danger hvr-sweep-to-top" type="submit"> <span v-if="!updating">{{ $t('save_changes') }}</span><span v-if="updating">{{ $t('sending') }}</span><div class="spinner-border text-light" role="status" v-if="updating"><span class="visually-hidden">Loading...</span></div></button></div></div></div></form></div></div></div></div></div></div></section><!--profile -->